import { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { getProjects } from "../redux/actions";

import SingleProjectComponent from "./SingleProjectComponent";

import "../css/projectSection.css";

export default function ProjectsSection({ initialState }) {
    const dispatch = useDispatch();
    const projects = useSelector((state) => {
        return state.data;
    });

    // const [error, setError] = useState(false);
    // const [projects, setProjects] = useState();

    console.log("projects in selector", projects);
    async function showProjectImage() {
        let images = [...document.querySelectorAll("img")];
        console.log(images);

        const observerArticleImg = new IntersectionObserver(showArticleImage);

        function showArticleImage(entries) {
            // console.log(entries);
            entries.forEach((entry) => {
                if (entry.isIntersecting) {
                    entry.target.classList.add("shrink");
                }
            });
        }
        images.forEach((image) => {
            // console.log(image);
            observerArticleImg.observe(image);
        });
    }

    useEffect(() => {
        dispatch(getProjects());
        showProjectImage();
    }, []);

    return (
        <>
            <section className="project-section" id="project-section">
                {projects &&
                    projects.map((project, index) => (
                        <SingleProjectComponent
                            project={project}
                            key={index}
                            initialState={initialState}
                        />
                    ))}
            </section>
        </>
    );
}
