import axios from "axios";
import { useEffect, useState } from "react";
import Navigation from "../components/Navigation";
import scrollHeadline from "../mixins/scrollHeadline";

export default function SingleProject({ projectId }) {
    console.log("id", projectId);
    const [project, setProject] = useState();
    const [stateTitle, setTitle] = useState("project");
    const [secTitle, setSecTitle] = useState("");
    const [err, setError] = useState(false);

    const initialState = {
        sectionHeader: "projects",
        links: [
            {
                id: 0,
                text: "Home",
                page: "/",
            },
            {
                id: 1,
                text: "Projects",
                page: "/projects",
            },
        ],
    };

    async function getSingleProject(projectId) {
        console.log("axios id", projectId);
        try {
            const { data } = await axios.get(`/api/projects/${projectId}`);
            console.log("data in singleProject", data);
            await data.map((project) => {
                return (
                    setProject(project),
                    setTitle(project.title),
                    setSecTitle(project.title_second),
                    scrollHeadline()
                );
            });
        } catch (err) {
            return setError(true);
        }
    }

    useEffect(() => {
        getSingleProject(projectId);
        window.scrollTo(0, 0);
    }, [projectId]);

    return (
        <>
            {err && <h1>Sorry! something went wrong...</h1>}
            <header className="header">
                <div className="intro-text">
                    {project && (
                        <h1>
                            <span>{stateTitle}</span>
                        </h1>
                    )}
                    {secTitle ? (
                        <h1>
                            <span>{secTitle}</span>
                        </h1>
                    ) : (
                        <></>
                    )}
                </div>
            </header>
            <Navigation initialState={initialState} />
            {project && (
                <section className="section single">
                    <div className="article-main-header">
                        <h1 className="header1 article-header">
                            <span>{stateTitle}</span>
                        </h1>
                        {project.title_second ? (
                            <h1 className="header1 article-header title2">
                                <span>{project.title_second}</span>
                            </h1>
                        ) : (
                            <></>
                        )}
                    </div>
                    <article>
                        <div className="article-image-container">
                            <img
                                className="article-image modal-image"
                                id="article-image"
                                src={project.image}
                                alt=""
                            />
                        </div>
                        <div className="article-content description">
                            <h4 className="article-headline">Description:</h4>
                            <p className="article-body">
                                {project.description}
                            </p>
                        </div>
                        <div className="article-content description">
                            <h4 className="article-headline">Technologies:</h4>
                            <p className="article-body">{project.technology}</p>
                        </div>
                        <div className="article-content">
                            <h4 className="article-headline">Find code on:</h4>
                            <a
                                className="article-body"
                                href="project.link"
                                target="_blank"
                            >
                                {project.link}
                            </a>
                        </div>
                        {project.host && (
                            <div className="article-content">
                                <h4 className="article-headline">
                                    Explore on:
                                </h4>
                                <a
                                    className="article-body"
                                    href="project.real"
                                    target="_blank"
                                >
                                    {project.host}
                                </a>
                            </div>
                        )}
                    </article>
                </section>
            )}
        </>
    );
}
