import React from "react";
import { BrowserRouter, Route, Redirect } from "react-router-dom";
import { useEffect } from "react";
import { useSelector } from "react-redux";

import Main from "./routes/Main";
import About from "./routes/About";
import Contact from "./routes/Contact";
import Projects from "./routes/Projects";
import SingleProject from "./routes/SingleProject";
import Login from "./routes/Login";
import ManageProjects from "./routes/ManageProjects";

import changeBackground from "./mixins/changeBackground";

import "./css/app.css";
import "./css/sectionComponent.css";

export default function App(state) {
    const admin = useSelector((state) => {
        return state.admin;
    });

    useEffect(() => {
        changeBackground();
    }, []);

    return (
        <BrowserRouter>
            <Route exact path="/" render={() => <Main />} />
            <Route path="/about" render={() => <About />} />
            <Route path="/contact" render={() => <Contact />} />
            <Route path="/projects" render={() => <Projects />} />
            <Route
                path="/singleProject/:id"
                render={(props) => (
                    <SingleProject projectId={props.match.params.id} />
                )}
            />
            <Route path="/login" render={() => <Login />} />
            {!admin ? (
                <Redirect to="/" />
            ) : (
                <Route
                    path="/manageProjects"
                    render={() => <ManageProjects />}
                />
            )}
        </BrowserRouter>
    );
}
